.gpt3__footer {
  background: linear-gradient(90deg, rgba(31, 31, 30, 1) 0%, rgba(49, 74, 78, 1) 50%, rgba(94, 40, 94, 1) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 4rem 0;
  position: relative;
  z-index: 1;
}

.gpt3__footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}

.gpt3__footer-heading h1{
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 54px;
  line-height: 75px;
}

.gpt3__footer-btn {
  display: flex;
  justify-content: center;
  border: 1px solid white;
  align-items: center;
  padding: 1rem;
  text-align: center;
  margin-bottom: 10rem;
  cursor: pointer;
}

.gpt3__footer-btn p {
  box-sizing: border-box;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 21px;
  color: #fff;
  word-spacing: 2px;
  cursor: pointer;
}

.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  text-align: left;
}

.gpt3__footer-links div {
  width: 250px;
  margin: 1rem;
}

.gpt3__footer-links-logo {
  display: flex;
  flex-direction: column;
}

.gpt3__footer-links-logo img {
  width: 118px;
  height: 30px;
  margin-bottom: 1rem;
}

.gpt3__footer-links_logo p {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 15px;
  color: #fff;
}
.gpt3__footer-links_div {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.gpt3__footer-links_div h4 {
  font-size: 14px;
  line-height: 17px;
  font-family: var(--font-family);
  color: #fff;

  margin-bottom: 0.9rem;
}

.gpt3__footer-links_div p {
  font-size: 12px;
  line-height: 15px;
  font-family: var(--font-family);
  color: #fff;

  margin: 0.5rem 0;
  cursor: pointer;
}

.gpt3__footer-copyright {
  margin-top: 2rem;
  text-align: center;
  width: 100%;
}

.gpt3__footer-copyright p {
  font-size: 12px;
  font-family: var(--font-family);
  line-height: 15px;
  color: #fff;
}

.bg-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.bg-image img {
  width: 80%;
  opacity: 0.5;
}

@media screen and (max-width: 850px) {
  .gpt3__footer-heading h1 {
      font-size: 44px;
      line-height: 50px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__footer-heading h1 {
      font-size: 34px;
      line-height: 42px;
  }

  .gpt3__footer-links div {
      margin: 1rem 0;
  }

  .gpt3__footer-btn p {
      font-size: 14px;
      line-height: 20px;
  }

  .bg-image img {
    display:none;
  }
  
}

@media screen and (max-width: 400px) {
  .gpt3__footer-heading h1 {
      font-size: 27px;
      line-height: 38px;
  }

  .gpt3__footer-links div {
    margin: 1rem 1rem;
  }

  .bg-image img {
    display:none;
  }

}
